import { Link } from "react-router-dom"

// @mui material components
import Card from "@mui/material/Card"
import CardMedia from "@mui/material/CardMedia"
import MDBox from "../../../../components/MDBox"
import MDTypography from "../../../../components/MDTypography"
import MDProgress from "../../../../components/MDProgress"
import locationIcon from "../../../../icons/locationIcon"
import timeIcon from "../../../../icons/timeIcon"
import { displayPerc } from "../../../../components/PercentageBar/PercentageBar"
function CompletedInvestmentCard({
  id,
  image,
  label,
  title,
  color = "info",
  personalProgress,
  addSpace,
  value,
  caption = "per month",
  duration,
  loading,
  priority
}) {
  // const formattedProgress = parseFloat(personalProgress).toFixed(1)
  // const displayProgress = formattedProgress.endsWith("0") ? formattedProgress.replace(/.0$/, "") : formattedProgress
  // const displayProgressInt = parseInt(displayProgress, 10)

  return (
    <Link to={`/investment/` + id} rel="noreferrer" sx={{ textDecoration: "none" }} data-test-id="lastInvestments">
      <Card
        sx={{
          display: "flex",
          flexDirection: "column",
          backgroundColor: "transparent",
          boxShadow: "none",
          overflow: "visible"
        }}>
        <MDBox sx={{ aspectRatio: "16/9", backgroundColor: "#e6e7e9" }} position="relative" width="100.25%" shadow="xl" borderRadius="xl">
          <CardMedia
            onError={({ currentTarget }) => {
              currentTarget.onerror = null // prevents looping
              currentTarget.src = image
            }}
            width="1890"
            height="1080"
            src={image}
            component="img"
            title={title}
            sx={{
              maxWidth: "100%",
              margin: 0,
              boxShadow: ({ boxShadows: { md } }) => md,
              objectFit: "cover",
              objectPosition: "center",
              aspectRatio: "16 / 9",
              height: "auto"
            }}
            fetchpriority={priority}
            loading={loading}
          />
        </MDBox>

        <MDBox mt={1} display="flex" justifyContent="space-between">
          <MDTypography variant="h5" textTransform="capitalize" fontSize={"1.15rem"}>
            {title}
          </MDTypography>
        </MDBox>
        <MDBox display="flex" justifyContent="space-between" flexDirection="column">
          <MDTypography fontSize={"0.68rem"} fontWeight="regular" color="text">
            {/* <LocationOnIcon sx={{ transform: "translateY(1.25px)" }} /> {label} */}
            {locationIcon} {label}
          </MDTypography>

          <MDTypography sx={{ display: "flex", gap: "4px", alignItems: "center" }} fontSize={"0.68rem"} fontWeight="regular" color="text">
            {timeIcon} {duration} months rent contract
          </MDTypography>
        </MDBox>
        <MDBox display="flex" justifyContent="space-between" mt="-1.2rem" flexDirection="column">
          <MDBox display="flex" justifyContent="end" mt="-2.75rem" height="1.6rem">
            <MDBox color={color}>
              <MDTypography component={"span"} lineHeight={1} fontSize={"1.15rem"} fontWeight="medium" color={color}>
                {value?.toLocaleString("en-US")}
              </MDTypography>
              <MDTypography pl={0.3} variant={"caption"} fontWeight="medium" color={color}>
                USDT
              </MDTypography>
            </MDBox>
          </MDBox>

          <MDBox display="flex" justifyContent="flex-end">
            <MDTypography lineHeight={1.4} fontSize={"0.68rem"} fontWeight="regular" color="text">
              {caption}
            </MDTypography>
          </MDBox>
        </MDBox>
        <MDBox display="flex" mt="1.8rem">
          {personalProgress > 0 ? (
            <MDTypography display="flex" mt="-0.1rem" fontSize="0.59rem" mr="0.5rem" variant="caption" color="text" fontWeight="medium">
              {/* {displayProgress}% */}
              {displayPerc(personalProgress).toLocaleString("en-US")}%
            </MDTypography>
          ) : (
            ""
          )}
          <MDBox width="100%" textAlign="left" pb={addSpace && 6.5}>
            <MDProgress value={personalProgress} color="text" variant="gradient" label={false} />
          </MDBox>
        </MDBox>
      </Card>
    </Link>
  )
}

export default CompletedInvestmentCard
