import { Grid } from "@mui/material"
import CompletedInvestmentCard from "../../../../examples/Cards/ProjectCards/CompletedInvestmentCard"
import MDBox from "../../../../components/MDBox"

function MyProfits({ myProperties }) {
  return myProperties?.map((property, index) => (
    <Grid key={property.house_id} item xs={12} md={6} xl={3}>
      <MDBox style={{ paddingBottom: index !== myProperties?.length - 1 ? "40px" : "12px" }}>
        <CompletedInvestmentCard
          id={property.house_id}
          image={property.pic}
          label={property.location}
          title={property.title}
          personalProgress={(property.personal_invested_amount / property.total_required) * 100}
          value={property.total_profits.my_profits}
          caption="earned"
          duration={property.duration}
          priority={index < 5 ? "high" : "auto"}
          loading={index < 5 ? "eager" : "lazy"}
        />
      </MDBox>
    </Grid>
  ))
}

export default MyProfits
