import { useMemo } from "react"
import PropTypes from "prop-types"
import { Line } from "react-chartjs-2"
import MDBox from "../../../../components/MDBox"
import configs from "../../../../examples/Charts/LineCharts/ReportsLineChartNoCard/configs"

function ReportsLineChartProfits({ color, title = "Profits", description, date, chart, amount = "4500$", children, profitsData }) {
  const { options } = configs(chart?.labels || [], chart?.datasets || { profitsData })
  const amounts = profitsData?.amounts || [] // Add a fallback value if 'amounts' is undefined
  const labels = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
  const updatedData = {
    labels,
    datasets: [
      {
        label: "Amount Profited",
        data: amounts,
        backgroundColor: "transparent",
        borderColor: "rgba(255, 255, 255, .8)",
        borderWidth: 4,
        fill: true,
        maxBarThickness: 6,
        pointBackgroundColor: "rgba(255, 255, 255, .8)",
        pointBorderColor: "transparent",
        pointRadius: 5,
        tension: 0
      }
    ]
  }

  return (
    <>
      {useMemo(
        () => (
          <MDBox variant="gradient" bgColor={color} borderRadius="lg" coloredShadow={color} py={2} pr={0.5} mt={-5} height="12.5rem">
            {profitsData && <Line data={updatedData} options={options} />}
          </MDBox>
        ),
        [color, options]
      )}

      <MDBox p={0.5} py={2} pb={1}>
        {children}
      </MDBox>
    </>
  )
}

// Setting default values for the props of ReportsLineChart
ReportsLineChartProfits.defaultProps = {
  color: "dark",
  description: ""
}

// Typechecking props for the ReportsLineChart
ReportsLineChartProfits.propTypes = {
  color: PropTypes.oneOf(["primary", "secondary", "info", "success", "warning", "error", "dark"]),
  title: PropTypes.string.isRequired,
  description: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  date: PropTypes.string.isRequired,
  chart: PropTypes.objectOf(PropTypes.oneOfType([PropTypes.array, PropTypes.object])).isRequired
}

export default ReportsLineChartProfits
